export default {
	data() {
		return {
			activeTab: 0
		};
	},
	props:{
		type:{
			type: String,
			default:'customer'
		},
		index:{
			type:Number,
			default:0
		}
	},
	mounted() {
		this.activeTab = this.index
	},
	methods:{
		switchTab(type){
			this.activeTab = type
			this.$emit('switchTab',type)
		}
	}
}
